<template>
  <div class="list-container">
    <div :key="source.id">
      <div class="name">
        <div>{{ source["zhName"] }}</div>
        <div class="sub-name">{{ source.name }}</div>
      </div>
      <div class="variations" style>
        <div
          class="variation-container"
          v-for="(obj, i) in source.variations"
          :key="i"
          @click="onVariationClick(obj.idx)"
