          <img
            class="variation"
            :class="{ 'variation--isOwnd': status[obj.idx] }"
            :src="obj.content.image"
          />
            <!-- v-lazy="obj.content.image" -->
          <span v-if="source.variations.length > 1" class="tooltip">
            {{
            obj.content.bodyColor_zh
            }}
          </span>

          <span v-if="source.variations.length > 1" class="variation-name">
            {{
            obj.content.bodyColor_zh
            }}
          </span>
        </div>
      </div>
    </div>
  </div>
