<script>
import { mapGetters } from "vuex";

export default {
  name: "ListRow",
  props: {
    onVariationClick: {
      type: Function,
      required: true
    },
    source: {
      type: Object,
      required: true
    }
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["filterListDatas"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  mounted() {
    // setTimeout(function() {
